    </div>
    <div v-show="showItem_movements">
      <v-card class="pb-4" height="60">
        <v-spacer></v-spacer>
        <div class="mt-5 mx-5">
          <template v-if="$auth.check(['ADMIN', 'CASHIER'])">
            <v-form>
              <v-row wrap>
                <v-autocomplete
                  v-model="formData.product"
                  :items="itemsProduct"
                  :loading="isLoading"
                  :search-input.sync="search"
                  color="#007bff"
                  item-text="name"
                  item-value="id"
                  return-object
                  :label="
                    $vuetify.lang.t('$vuetify.supplies.cols.product_name.title')
                  "
                  class="font-weight-bold my-2"
                  outlined
                  dense
                  clearable
                  style="max-width: 150px"
                ></v-autocomplete>
                <v-text-field
                  color="#007bff"
                  :label="
                    $vuetify.lang.t('$vuetify.supplies.cols.product_code.title')
                  "
                  class="font-weight-bold my-2 mr-1 ml-1"
                  outlined
                  dense
                  v-model="get_product_code"
