          :items="itemsItemSupplies"
          :items-per-page="itemPerPage"
          :loading="loadingItemSupplies.list"
          :options.sync="pagination"
          :server-items-length="meta.totalElements"
          :sort-by="sortBy"
          :sort-desc="sortDesc"
          item-key="name"
          class="mt-5"
        >
          <template v-slot:header.code="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr>
          </template>
          <template v-slot:header.createdAt="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr>
          </template>
          <template v-slot:header.quantity="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr>
          </template>
          <template v-slot:header.arrival_price="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr>
          </template>
          <template v-slot:header.total_amount="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr>
          </template>
          <template v-slot:body="{ items }">
            <v-hover>
              <tbody>
                <tr :key="item.id" v-for="item in items">
                  <td class="subtitle-2">
                    {{ item.supplies.code }}
                  </td>
                  <td class="subtitle-2">{{ item.supplies.createdAt }}</td>

                  <td class="subtitle-2">
                    {{ item.quantity }}
                  </td>
                  <td class="subtitle-2">
                    {{ item.arrival_price }}
                  </td>
                  <td class="subtitle-2">
                    {{ item.total_amount }}
                  </td>
                </tr>
              </tbody>
            </v-hover>
          </template>
        </v-data-table>
      </v-card>
      <div
        class="title mb-5 black--text blue lighten-5 text-right"
        style="height: 30px; margin-top: 30px"
      >
        {{ $vuetify.lang.t("$vuetify.stock_movement.list.title") }}
      </div>
      <v-card class="pb-4">
        <v-data-table
          :footer-props="{
            'items-per-page-options': itemPerPageOptions
          }"
          :headers="headersItemStock_movement"
          :items="itemsItemStock_movement"
          :items-per-page="itemPerPage"
          :loading="loadingItemStock_movement.list"
          :options.sync="pagination"
          :server-items-length="meta.totalElements"
          :sort-by="sortBy"
          :sort-desc="sortDesc"
          item-key="name"
          class="mt-5"
        >
          <template v-slot:header.code="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr>
          </template>
          <template v-slot:header.date_movement="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr>
          </template>
          <template v-slot:header.quantity="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr>
          </template>
          <template v-slot:header.unit_price="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr>
          </template>
          <template v-slot:header.total_amount="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr>
          </template>
          <template v-slot:body="{ items }">
            <v-hover>
              <tbody>
                <tr
                  :key="itemStock_movement.id"
                  v-for="itemStock_movement in items"
                >
                  <td class="subtitle-2">
                    {{ itemStock_movement.stock_movement.code }}
                  </td>
                  <td class="subtitle-2">
                    {{ itemStock_movement.stock_movement.date_movement }}
                  </td>

                  <td class="subtitle-2">
                    {{ itemStock_movement.quantity }}
                  </td>
                  <td class="subtitle-2">
                    {{ itemStock_movement.unit_price }}
                  </td>
                  <td class="subtitle-2">
                    {{ itemStock_movement.total_amount }}
                  </td>
                </tr>
              </tbody>
            </v-hover>
          </template>
        </v-data-table>
        <delete-item
          :dialog="dialogDelete"
          :messages="messagesDelete"
          :urlItems="urlDeleteItems"
          @getItemsDetails="getItemsDetails"
        ></delete-item>
      </v-card>
    </div>
  </div>
</template>

<script>
import ListMixin from "./../../mixins/Common/List.vue";
// import FormUser from "./Form";
import FilterForm from "./Filter";
import DeleteItem from "./../Common/Delete";
import FormMovement from "./FormMovement";
import FormInventory from "./FormInventory";
const FileSaver = require("file-saver");

export default {
  mixins: [ListMixin],
  data: () => ({
    formData: { start_date: null, end_date: null, product: null },
    itemsStock_available: [],
    itemsStock_movement: [],
    itemsInventory: [],
    itemsItemBills: [],
    itemsItemSupplies: [],
    itemsItemStock_movement: [],
    headersStock_available: [],
    headersItemStock_movement: [],
    headersItemSupplies: [],
    headersItemBills: [],
    headersStock_movement: [],
    headersInventory: [],
    itemsProduct: [],
    showStock_available: false,
    showStocks_movements: false,
    showInventories: false,
    showItem_movements: false,
    urlProduct: "products",
    urlItemsBills: "details_bills/get_items",
    urlItemsSupplies: "details_supplies/get_items",
    urlItemsStock_movement: "details_stock_movement/get_items",
    urlItemsStock: "details_supplies/stock_available",
    urlStock_movement: "stock_movements/get_stock_movement",
    urlInventory: "inventories/get_inventory",
    sortBy: ["id"],
    menu2: false,
    search: null,
    isLoading: false,
    menu: false,
    loadingStockAvailable: {
      list: false,
      refresh: false,
      filter: false
    },
    loadingBtn: false,
    loadingItemStock_movement: {
      list: false,
      refresh: false,
      filter: false
    },
    loadingItemSupplies: {
      list: false,
      refresh: false,
      filter: false
    },
    loadingItemBills: {
      list: false,
      refresh: false,
      filter: false
    },
    loadingInventory: {
      list: false,
      refresh: false,
      filter: false
    },
    loadingStockMovement: {
      list: false,
      refresh: false,
      filter: false
    },
    dialogForm: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    dialogFormInventory: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    dialogFormStock_movement: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    dialogDelete: {
      show: false
    },
    dialogDetail: {
      show: false
    },
    dialogEnt: {
      show: false
    },
    form: {
      id: null,
      type: null,
      email: null,
      code: null,
      password: null,
      role: null,
      is_active: null
    },
    filter: {
      code: null,
      role: null
    },
    messages: {
      title: "",
      submit: "",
      success: ""
    },
    messagesDelete: {
      success: "$vuetify.storage_depots.delete.success"
    }
  }),
  watch: {
    search(value) {
      if (value === null) return;
      const params = { name: value };
      let self = this;
      this.isLoading = true;
      this.$store
        .dispatch("request", {
          url: self.urlProduct,
          params: params
        })
        .then(response => {
          self.itemsProduct = response.data.content;
          // console.log(this.form.product);
        })
        .finally(() => (this.isLoading = false));
    },
    pagination: {
      handler() {
        this.getItems();
      },
      deep: true
    }
  },
  created() {
    this.setHeadersItemBills();
    this.setHeadersItemSupplies();
    this.setHeadersItemStock_movement();

    //this.setItemRoles();
  },
  methods: {
    itemMovement() {
      let self = this;
      this.getItemBills();
      this.getItemStock_movement();
      this.getItemSupplies();
      this.loadingItemBills.refresh = true;
      this.getItemBills()
        .then(() => {})
        .catch(() => {})
        .then(() => {
          self.loadingItemBills.refresh = false;
        });
      this.loadingItemSupplies.refresh = true;
      this.getItemSupplies()
        .then(() => {})
        .catch(() => {})
        .then(() => {
          self.loadingItemSupplies.refresh = false;
        });
      this.loadingItemStock_movement.refresh = true;
      this.getItemStock_movement()
        .then(() => {})
        .catch(() => {})
        .then(() => {
          self.loadingItemStock_movement.refresh = false;
        });
    },
    exportItemMovement() {
      let self = this;
      self.$store
        .dispatch("request", {
          url:
            self.urlProduct +
            "/" +
            "export_items" +
            "?product=" +
            this.formData.product["id"] +
            "&start_date=" +
            this.formData.start_date +
            "&end_date=" +
            this.formData.end_date +
            "&id=" +
            this.$route.params.id,
          responseType: "blob"
        })
        .then(response => {
          let fileName = "Historique des sessions";
          let blob = new Blob([response.data], { type: response.data.type });
          console.log(response.data);
          FileSaver.saveAs(blob, fileName);
        });
    },
    getItemBills() {
      let self = this;
      const { page, itemsPerPage } = this.pagination;
      // console.log(page);
      // console.log(itemsPerPage);
      let params = {
        page: page,
        size: itemsPerPage
      };
      if (!this._.isEmpty(this._search)) {
        this._search = this._.merge(params, this._search);
      }
      return new Promise((resolve, reject) => {
        this.loadingItemBills.list = true;
        //console.log("ls params", params);
        //console.log("Sorts", sortBy, sortDesc);
        self.$store
          .dispatch("request", {
            url:
              self.urlItemsBills +
              "?product=" +
              this.formData.product["id"] +
              "&start_date=" +
              this.formData.start_date +
              "&end_date=" +
              this.formData.end_date +
              "&id=" +
              this.$route.params.id,
            params: params
          })
          .then(response => {
            let data = response.data;
            self.itemsItemBills = data.content;
            self.meta = { totalElements: data.totalElements };
            // console.log("meta", data);
            resolve(response);
          })
          .catch(err => {
            let message = this.$vuetify.lang.t("$vuetify.error_occured");
            if (err.response) {
              if (err.response.status === 400) {
                self.setApiFormErrors(err.response.data.data);
                message = self.$vuetify.lang.t("$vuetify.error_form");
              } else if (err.response.status === 403) {
                message = self.$vuetify.lang.t("$vuetify.error_denied");
              } else if (err.response.status === 500) {
                message = self.$vuetify.lang.t("$vuetify.error_server");
              }
            }
            self.$store.dispatch("showNotification", {
              status: true,
              text: message
            });
            reject(err);
          })
          .then(() => {
            self.loadingItemBills.list = false;
            resolve();
          });
      });
    },
    getItemSupplies() {
      let self = this;
      const { page, itemsPerPage } = this.pagination;
      // console.log(page);
      // console.log(itemsPerPage);
      let params = {
        page: page,
        size: itemsPerPage
      };
      if (!this._.isEmpty(this._search)) {
        this._search = this._.merge(params, this._search);
      }
      return new Promise((resolve, reject) => {
        this.loadingItemSupplies.list = true;
        //console.log("ls params", params);
        //console.log("Sorts", sortBy, sortDesc);
        self.$store
          .dispatch("request", {
            url:
              self.urlItemsSupplies +
              "?product=" +
              this.formData.product["id"] +
              "&start_date=" +
              this.formData.start_date +
              "&end_date=" +
              this.formData.end_date +
              "&id=" +
              this.$route.params.id,
            params: params
          })
          .then(response => {
            let data = response.data;
            self.itemsItemSupplies = data.content;
            self.meta = { totalElements: data.totalElements };
            // console.log("meta", data);
            resolve(response);
          })
          .catch(err => {
            let message = this.$vuetify.lang.t("$vuetify.error_occured");
            if (err.response) {
              if (err.response.status === 400) {
                self.setApiFormErrors(err.response.data.data);
                message = self.$vuetify.lang.t("$vuetify.error_form");
              } else if (err.response.status === 403) {
                message = self.$vuetify.lang.t("$vuetify.error_denied");
              } else if (err.response.status === 500) {
                message = self.$vuetify.lang.t("$vuetify.error_server");
              }
            }
            self.$store.dispatch("showNotification", {
              status: true,
              text: message
            });
            reject(err);
          })
          .then(() => {
            self.loadingItemSupplies.list = false;
            resolve();
          });
      });
    },
    getItemStock_movement() {
      let self = this;
      const { page, itemsPerPage } = this.pagination;
      // console.log(page);
      // console.log(itemsPerPage);
      let params = {
        page: page,
        size: itemsPerPage
      };
      if (!this._.isEmpty(this._search)) {
        this._search = this._.merge(params, this._search);
      }
      return new Promise((resolve, reject) => {
        this.loadingItemStock_movement.list = true;
        //console.log("ls params", params);
        //console.log("Sorts", sortBy, sortDesc);
        self.$store
          .dispatch("request", {
            url:
              self.urlItemsStock_movement +
              "?product=" +
              this.formData.product["id"] +
              "&start_date=" +
              this.formData.start_date +
              "&end_date=" +
