<template>
  <v-dialog v-model="dialog.show" persistent max-width="950px">
    <v-card>
      <v-toolbar dark color="#f5f5f5" dense>
        <div class="title" style="color: black">
          {{ $vuetify.lang.t(messages.title) }}
        </div>
        <v-spacer></v-spacer>
        <v-btn icon light @click="forceRefresh() + resetDataForm()">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-form>
        <v-card-text max-width="1500px">
          <v-container>
            <v-row wrap>
              <v-col cols="12" sm="4" class="mb-0 py-0">
                <v-text-field
                  v-model="form.code"
                  color="#007bff"
                  :label="
                    $vuetify.lang.t('$vuetify.stock_movement.cols.code.title')
                  "
                  prepend-inner-icon="mdi-code-array"
                  class="font-weight-bold"
                  outlined
                  dense
                  filled
                  readonly
                ></v-text-field>
              </v-col>

              <v-col cols="12" sm="4" class="mb-0 py-0">
                <v-menu
                  v-model="menu3"
                  color="#007bff"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  transition="scale-transition"
                  offset-y
                  min-width="auto"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="form.date_movement"
                      color="#007bff"
                      :label="
                        $vuetify.lang.t(
                          '$vuetify.stock_movement.cols.date_movement.title'
                        )
                      "
                      prepend-inner-icon="mdi-clock-start"
                      class="font-weight-bold"
                      outlined
                      v-bind="attrs"
                      v-on="on"
                      dense
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="form.date_movement"
                    color="#007bff"
                    @input="menu3 = false"
                  ></v-date-picker>
                </v-menu>
              </v-col>
              <v-col cols="12" sm="4" class="mb-0 py-0">
                <v-text-field
                  color="#007bff"
                  :label="
                    $vuetify.lang.t(
                      '$vuetify.stock_movement.cols.movement_value.title'
                    )
                  "
                  prepend-inner-icon="mdi-credit-card"
                  class="font-weight-bold"
                  outlined
                  dense
                  :value="get_movement_value"
                  readonly
                  suffix="FCFA"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="4" class="mb-0 py-0">
                <v-select
                  v-model="form.type_movement"
                  color="#007bff"
                  :items="optionMovement.type_Movement"
                  :label="
                    $vuetify.lang.t('$vuetify.cash_movement.cols.type.title')
                  "
                  dense
                  prepend-inner-icon="mdi-account-supervisor-circle"
                  outlined
                  class="font-weight-bold"
                ></v-select>
              </v-col>
              <v-col cols="12" sm="8" class="mb-0 py-0">
                <v-text-field
                  v-model="form.reason_movement"
                  color="#007bff"
                  :label="
                    $vuetify.lang.t(
                      '$vuetify.stock_movement.cols.reason_movement.title'
                    )
                  "
                  prepend-inner-icon="mdi-script-text"
                  class="font-weight-bold"
                  outlined
                  dense
                  :readonly="supply"
                ></v-text-field>
              </v-col>
              <!--              <v-col-->
              <!--                cols="12"-->
              <!--                sm="1"-->
              <!--                class="mb-0 py-0"-->
              <!--                v-if="this.form.id === null"-->
              <!--              >-->
              <!--                <v-btn-->
              <!--                  color="#007bff"-->
              <!--                  :loading="loadingBtn"-->
              <!--                  dark-->
              <!--                  type="submit"-->
              <!--                  dense-->
              <!--                  small-->
              <!--                  max-width="2px"-->
              <!--                  style="margin-top: 5px"-->
              <!--                  depressed-->
              <!--                  :disabled="-->
              <!--                    form.product === null ||-->
              <!--                      form.date_movement === null ||-->
              <!--                      form.type_movement === null ||-->
              <!--                      get_movement_value === 0 ||-->
              <!--                      form.reason_movement === null-->
              <!--                  "-->
              <!--                >-->
              <!--                  <v-icon dark>-->
              <!--                    mdi-content-save-->
              <!--                  </v-icon>-->

              <!--                  &lt;!&ndash;{{ $vuetify.lang.t(messages.submit) }}&ndash;&gt;-->
              <!--                </v-btn>-->
              <!--              </v-col>-->
            </v-row>
          </v-container>
        </v-card-text>
        <!--<v-card-actions style="margin-top: -45px">-->
        <!--<v-spacer></v-spacer>-->
        <!--<v-btn color="#007bff" :loading="loading" dark type="submit" small style="margin-left: 18px">-->
        <!--{{ $vuetify.lang.t(messages.submit) }}-->
        <!--</v-btn>-->
        <!--</v-card-actions>-->
        <v-divider class="mx-4" style="margin-top: -30px"></v-divider>
      </v-form>
      <v-form @submit.prevent="saveDetails" style="margin-top: -5px">
        <v-card-text>
          <v-container>
            <v-row wrap>
              <v-col cols="12" sm="3" class="mb-0 py-0">
                <v-autocomplete
                  v-model="form.details_stock"
                  :items="itemsProduct"
                  :loading="isLoading"
                  :search-input.sync="search"
                  color="#007bff"
                  item-text="product_name"
                  item-value="id"
                  return-object
                  :label="
                    $vuetify.lang.t(
                      '$vuetify.stock_movement.cols.product_name.title'
                    )
                  "
                  prepend-inner-icon="mdi-rename-box"
                  class="font-weight-bold"
                  outlined
                  dense
                  clearable
                ></v-autocomplete>
              </v-col>
              <v-col cols="12" sm="2" class="mb-0 py-0">
                <v-text-field
                  color="#007bff"
                  :label="
                    $vuetify.lang.t(
                      '$vuetify.stock_movement.cols.product_code.title'
                    )
                  "
                  prepend-inner-icon="mdi-code-array"
                  class="font-weight-bold"
                  outlined
                  dense
                  :value="get_product_code"
                  readonly
                ></v-text-field>
              </v-col>
              <!--<v-col-->
              <!--cols="12"-->
              <!--sm="3"-->
              <!--class="mb-0 py-0"-->
              <!--v-if="form.product_code != null"-->
              <!--&gt;-->
              <!--<v-text-field-->
              <!--color="#007bff"-->
              <!--v-model="form.product_code"-->
              <!--:label="-->
              <!--$vuetify.lang.t('$vuetify.stock_movement.cols.product_code.title')-->
              <!--"-->
              <!--prepend-inner-icon="mdi-code-array"-->
              <!--class="font-weight-bold"-->
              <!--outlined-->
              <!--dense-->
              <!--&gt;</v-text-field>-->
              <!--</v-col>-->

              <v-col cols="12" sm="2" class="mb-0 py-0">
                <v-text-field
                  v-model="form.quantity"
                  color="#007bff"
                  :label="
                    $vuetify.lang.t(
                      '$vuetify.stock_movement.cols.quantity.title'
                    )
                  "
                  prepend-inner-icon="mdi-numeric-0-box"
                  class="font-weight-bold"
                  outlined
                  dense
                  type="number"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="2" class="mb-0 py-0">
                <v-text-field
                  color="#007bff"
                  :label="
                    $vuetify.lang.t(
                      '$vuetify.stock_movement.cols.unit_price.title'
                    )
                  "
                  prepend-inner-icon="mdi-credit-card"
                  class="font-weight-bold"
                  outlined
                  dense
                  v-model="get_product_price"
                  readonly
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="2" class="mb-0 py-0">
                <v-text-field
                  v-model="get_total_amount"
                  color="#007bff"
                  :label="
                    $vuetify.lang.t(
                      '$vuetify.stock_movement.cols.total_amount.title'
                    )
                  "
                  prepend-inner-icon="mdi-credit-card-plus"
                  class="font-weight-bold"
                  outlined
                  :value="get_total_amount"
                  dense
                  suffix="FCFA"
                  readonly
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="1" class="mb-0 py-0">
                <v-tooltip top>
                  <template v-slot:activator="{ on }">
                    <v-btn
                      color="#007bff"
                      :loading="loadingBtn"
                      dark
                      type="submit"
                      dense
                      small
                      v-on="on"
                      max-width="2px"
                      style="margin-top: 3px"
                      :disabled="
                        form.details_stock === null ||
                          form.date_movement === null ||
                          form.type_movement === null ||
                          form.reason_movement === null
                      "
                    >
                      <v-icon dark>
                        mdi-content-save
                      </v-icon>
                      <!--{{ $vuetify.lang.t(messages.submit) }}-->
                    </v-btn>
                  </template>
                  <span
                    class="
                        text-center
                        font-weight-bold
                        body-2
                        withe--text
                        text--d
                        ken-1
                      "
                  >
                    {{ $vuetify.lang.t("$vuetify.bills.new.add_product") }}
                  </span>
                </v-tooltip>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-divider class="mx-4" style="margin-top: -28px"></v-divider>
      </v-form>
      <div>
        <v-card max-width="1110px" style="margin-left: 18px">
          <div class="mt-5 mx-5">
            <v-btn
              :loading="loadingDetails.refresh"
              @click="refreshItemsDetail()"
              class="mr-2 my-2"
              depressed
              color="#f1f5fc"
              dense
            >
              <v-icon left color="#007bff">mdi-refresh</v-icon>
              {{ $vuetify.lang.t("$vuetify.btn.refresh") }}
            </v-btn>
            <v-btn
              color="#f1f5fc"
              @click="showFilter()"
              class="mr-2 my-2"
              depressed
              dense
            >
              <v-icon color="#007bff" left>mdi-filter-outline</v-icon>
              {{ $vuetify.lang.t("$vuetify.stock_movement.btn.filter.title") }}
            </v-btn>
            <v-btn
              color="#007bff"
              @click="save()"
              class="mr-2 my-2"
              depressed
              dense
              dark
              :loading="loadingBtn"
              style="margin-left: 365px"
              :disabled="
                form.product === null ||
                  form.date_movement === null ||
                  form.type_movement === null ||
                  get_movement_value === 0 ||
                  form.reason_movement === null
              "
            >
              <v-icon>
                mdi-content-save
              </v-icon>
              {{
                $vuetify.lang.t(
                  "$vuetify.stock_movement.new.submitstock_movement"
                )
              }}
            </v-btn>
            <v-spacer></v-spacer>
          </div>
          <v-data-table
            :footer-props="{
              'items-per-page-options': itemPerPageOptions
            }"
            :headers="headers"
            :items="items"
            :items-per-page="itemPerPage"
            :loading="loadingDetails.list"
            :options.sync="pagination"
            :server-items-length="meta.totalElements"
            :sort-by="sortBy"
            :sort-desc="sortDesc"
            item-key="name"
            class="mt-5"
          >
            <template v-slot:header.product_name="{ header }">
              <tr>
                {{
                  $vuetify.lang.t(header.text)
                }}
              </tr>
            </template>
            <template v-slot:header.product_code="{ header }">
              <tr>
                {{
                  $vuetify.lang.t(header.text)
                }}
              </tr>
            </template>
            <template v-slot:header.quantity="{ header }">
              <tr>
                {{
                  $vuetify.lang.t(header.text)
                }}
              </tr>
            </template>
            <template v-slot:header.unit_price="{ header }">
              <tr>
                {{
                  $vuetify.lang.t(header.text)
                }}
              </tr>
            </template>
            <template v-slot:header.total_amount="{ header }">
              <tr>
                {{
                  $vuetify.lang.t(header.text)
                }}
              </tr>
            </template>
            <!--            <template v-slot:header.create_date="{ header }">-->
            <!--              <tr>-->
            <!--                {{-->
            <!--                  $vuetify.lang.t(header.text)-->
            <!--                }}-->
            <!--              </tr>-->
            <!--            </template>-->
            <template v-slot:header.null="{ header }">
              <tr>
                {{
                  $vuetify.lang.t(header.text)
                }}
              </tr>
            </template>
            <template v-slot:body="{ items }">
              <v-hover>
                <tbody slot-scope="{ hover }">
                  <tr
                    :key="itemStock_movement.id"
                    v-for="itemStock_movement in items"
                  >
                    <td class="subtitle-2">
                      {{ itemStock_movement.details_stock.product.name }}
                    </td>
                    <td class="subtitle-2">
                      {{ itemStock_movement.details_stock.product.code }}
                    </td>

                    <td class="subtitle-2">
                      {{ itemStock_movement.quantity }}
                    </td>
                    <td class="subtitle-2">
                      {{ itemStock_movement.unit_price }}
                    </td>
                    <td class="subtitle-2">
                      {{ itemStock_movement.total_amount }}
                    </td>
                    <!--                    <td class="subtitle-2">-->
                    <!--                      {{ $moment(item.createdAt).format("YYYY-MM-DD") }}-->
                    <!--                    </td>-->
                    <td
                      class="subtitle-2"
                      v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])"
                      width="50px"
                    >
                      <template>
                        <v-tooltip bottom>
                          <template
                            v-slot:activator="{ on }"
                            v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])"
                          >
                            <v-btn
                              @click="editDetails(itemStock_movement)"
                              color="secondary"
                              icon
                              text
                              v-on="on"
                              v-show="$vuetify.breakpoint.smAndDown || hover"
                            >
                              <v-icon mdi-18px>mdi-grease-pencil</v-icon>
                            </v-btn>
                          </template>
                          <span>{{
                            $vuetify.lang.t("$vuetify.btn.edit")
                          }}</span>
                        </v-tooltip>
                        <v-tooltip bottom>
                          <template
                            v-slot:activator="{ on }"
                            v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])"
                          >
                            <v-btn
                              @click="deleteItem(itemStock_movement)"
                              color="red"
                              icon
                              text
                              v-on="on"
                              dark
                              v-show="$vuetify.breakpoint.smAndDown || hover"
                            >
                              <v-icon mdi-18px>mdi-delete-outline</v-icon>
                            </v-btn>
                          </template>
                          <span>{{
                            $vuetify.lang.t("$vuetify.btn.remove")
                          }}</span>
                        </v-tooltip>
                      </template>
                    </td>
                    <td class="subtitle-2" v-else></td>
                  </tr>
                </tbody>
              </v-hover>
            </template>
          </v-data-table>
          <delete-item
            :dialog="dialogDelete"
            :messages="messagesDelete"
            :urlItems="urlDeleteItems"
            @getItemsDetails="getItemsDetails"
          ></delete-item>
        </v-card>
      </div>
    </v-card>
  </v-dialog>
</template>

<script>
// import ListMixin from "./../../mixins/Common/List.vue";
import FormMixin from "./../../mixins/Common/Form.vue";
// import {mapGetters} from "vuex";
// import { required } from "vuelidate/lib/validators";
import DeleteItem from "./../Common/DeleteDetails";

export default {
  mixins: [FormMixin],
  props: {
    form: {
      type: Object,
      default: function() {
        return {
          id: null,
          stock_movement: null,
          total_amount: null,
          quantity: null,
          details_stock: null,
          product_code: null,
          date_movement: null,
          type_movement: null,
          reason_movement: null,
          movement_value: null,
          code: null,
          storage_depots: null
        };
      }
    }
  },
  data: () => ({
    formData: {
      id: null,
      name: null,
      coefficient: null,
      number_account: null,
      quote_internal: null,
      quote_external: null
    },
    headers: [],
    items: [],
    meta: {},
    itemPerPage: 10,
    //itemPerPages: 3,
    itemPerPageOptions: [5, 10, 30],
    urlDeleteItems: "",
    sortDesc: true,
    loading: {
      list: false,
      refresh: false,
      filter: false
    },
    pagination: {},
    selectedItem: false,
    _search: {},
    _sorts: {},
    dialogDelete: {
      show: false
    },
    sortBy: ["id"],
    e1: 1,
    isLoading: false,
    search: null,
    menu2: false,
    supply: false,
    active: false,
    menu3: false,
    menu4: false,
    menu: false,
    dialogForm: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    dialogFormDetails: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    messagesDelete: {
      success: "$vuetify.stock_movement.delete.success"
    },
    loadingBtn: false,
    // formR: {
    //   id: null,
    //   name: null
    // },
    loadingDetails: {
      list: false,
      refresh: false,
      filter: false
    },
    optionMovement: {
      type_Movement: []
    },
    itemsProduct: [],
    urlProduct: "products/get_products",
    urlItems: "stock_movements",
    urlItemsDetails_stock_movement: "details_stock_movement"
  }),
  watch: {
    pagination: {
      handler() {
        this.getItemsDetails();
      },
      deep: true
    },
    search(value) {
      if (value === null) return;
      const params = { name: value };
      let self = this;
      this.isLoading = true;
      this.$store
        .dispatch("request", {
          url: self.urlProduct + "?storage_depots=" + this.$route.params.id,
          params: params
        })
        .then(response => {
          self.itemsProduct = response.data.content;
          // console.log(response.data.content);
        })
        .finally(() => (this.isLoading = false));
    }
  },
  created() {
    this.setHeaders();
    this.getOptionsMovement();
    //this.setItemRoles();
  },
  validations() {
    let validators = {
      form: {}
    };
    return validators;
  },
  methods: {
    getOptionsMovement() {
      // On pouvait aussi utliser le forEach au lieu de   _.eaach
      let self = this;
      this._.each(this.$store.getters.type_Movement, type_Movement => {
        self.optionMovement.type_Movement.push({
          text: self.$vuetify.lang.t(type_Movement.text),
          value: type_Movement.value
        });
      });
    },
    forceRefresh() {
      this.dialog.show = false;
      this.items = [];
      this.active = false;
      this.resetDataForm();
    },
    deleteItem(item) {
      this.urlDeleteItems = this.urlItemsDetails_stock_movement + "/" + item.id;
      this.dialogDelete.show = true;
    },
    setHeaders() {
      this.headers = [
        {
          text: "$vuetify.stock_movement.cols.product_name.title",
          value: "product_name",
          align: "start",
          sortable: false,
          width: "150",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.stock_movement.cols.product_code.title",
          value: "product_code",
          align: "start",
          sortable: false,
          width: "100",
          class: "grey--text text--darken-3"
        },

        {
          text: "$vuetify.stock_movement.cols.quantity.title",
          value: "quantity",
          align: "start",
          sortable: false,
          width: "100",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.stock_movement.cols.unit_price.title",
          value: "unit_price",
          align: "start",
          sortable: false,
          width: "20",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.stock_movement.cols.total_amount.title",
          value: "total_amount",
          align: "start",
          sortable: false,
          width: "30",
          class: "grey--text text--darken-3"
        },
        // {
        //   text: "$vuetify.stock_movement.cols.created.title",
        //   value: "create_date",
        //   align: "start",
        //   sortable: false,
        //   width: "105",
        //   class: "grey--text text--darken-3"
        // },
        {
          text: "$vuetify.stock_movement.cols.actions.title",
          value: null,
          align: "start",
          sortable: false,
          width: "70"
        }
      ];
    },
    createD() {
      this.dialogForm.show = true;
      this.formData = {
        id: null,
        name: null,
        coefficient: null,
        number_account: null,
        quote_internal: null,
        quote_external: null
      };
    },
    // Fonction pour récuperer le contenu des champs du formulaire
    getDataForm() {
      let data = {
        date_movement: this.form.date_movement,
        type_movement: this.form.type_movement,
        suppliers: this.form.suppliers,
        reason_movement: this.form.reason_movement,
        movement_value: this.get_movement_value,
        storage_depots: this.$route.params.id
      };
      return data;
    },
    getDataFormdetails() {
      let data = {
        details_stock: this.form.details_stock["id"],
        total_amount: this.get_total_amount,
        quantity: this.form.quantity,
        type_movement: this.form.type_movement,
        storage_depots: this.$route.params.id,
        createdAt: this.form.date_movement,
        unit_price: this.get_product_price
      };

      return data;
    },
    setForm(item) {
      // Données envoyées lors de la modification
      this.form.id = item.id;
      this.form.stock_movement = item.id;
      this.form.type_movement = item.type_movement;
      this.form.date_movement = item.date_movement;
      this.form.movement_value = item.movement_value;
      this.form.reason_movement = item.reason_movement;
      this.form.code = item.code;
      this.getItemsDetails();
    },
    getRouteDetails() {
      if (this.form.id) {
        return this.urlItemsDetails_stock_movement + "/" + this.form.id;
      } else {
        return this.urlItemsDetails_stock_movement;
      }
    },
    getRoute() {
      if (this.form.id) {
        return this.urlItems + "/" + this.form.id;
      } else {
        return this.urlItems;
      }
    },
    resetDataFormProduct() {
      this.form.details_stock = null;
      this.form.quantity = null;
    },
    resetDataForm() {
      this.form.id = null;
      this.items = [];
      if (this.form.id) return;
      let self = this;
      this.$v.form.$reset();
      this._.each(this.form, (field, k) => {
        if (typeof self.form[k] === "object") {
          self._.each(self.form[k], (subField, i) => {
            if (typeof self.form[k][i] === "boolean") {
              self.form[k][i] = false;
            } else {
              self.form[k][i] = null;
            }
          });
        } else {
          if (typeof self.form[k] === "boolean") {
            self.form[k] = false;
          } else {
            self.form[k] = null;
          }
        }
      });
    },
    refreshItemsDetail() {
      let self = this;
      this.loadingDetails.refresh = true;
      this.getItemsDetails()
        .then(() => {})
        .catch(() => {})
        .then(() => {
          self.loadingDetails.refresh = false;
        });
    },
    editDetails(itemStock_movement) {
      console.log(itemStock_movement.details_stock);
      this.form.details_stock = itemStock_movement.details_stock;
    },
    save() {
      this.$store.dispatch("showNotification", {
        statut: false
      });
      let self = this;
      self.$v.$touch();
      if (!self.$v.$invalid) {
        this.loading = true;
        this.$store
          .dispatch("request", {
            url: self.getRoute(),
            method: self.getMethod(),
            data: self.getDataForm()
          })
          .then(() => {
            self.$store.dispatch("showNotification", {
              statut: true,
              text: self.$vuetify.lang.t("$vuetify.stock_movement.new.success")
            });
            this.getStockMovement();
            self.resetDataForm();
          })
          .catch(error => {
            let message;
            if (error.response.status === 400) {
              message = "$vuetify.error_occured";
              const fields = error.response.data;
              self.setFormErrors(fields);
              this.$forceUpdate();
            } else {
              message = "$vuetify.error_server";
            }
            self.$store.dispatch("showNotification", {
              statut: true,
              text: self.$vuetify.lang.t(message),
              color: "#B71C1C"
            });
          })
          .then(() => {
            self.loading = false;
          });
      }
      /*else {
               console.log(self.$v);
             }*/
    },
    getItemsDetails() {
      let self = this;
      const { page, itemsPerPage } = this.pagination;
      // console.log(page);
      // console.log(itemsPerPage);
      let params = {
        page: page,
        size: itemsPerPage
      };
      if (!this._.isEmpty(this._search)) {
        this._search = this._.merge(params, this._search);
      }
      return new Promise((resolve, reject) => {
        this.loadingDetails.list = true;
        //console.log("ls params", params);
        //console.log("Sorts", sortBy, sortDesc);
        self.$store
          .dispatch("request", {
            url:
              self.urlItemsDetails_stock_movement +
              "?stock_movement=" +
              this.form.stock_movement,
            params: params
          })
          .then(response => {
            let data = response.data;
            self.items = data.content;
            self.meta = { totalElements: data.totalElements };
            // console.log("meta", data);
            resolve(response);
          })
          .catch(err => {
            let message = this.$vuetify.lang.t("$vuetify.error_occured");
            if (err.response) {
              if (err.response.status === 400) {
                self.setApiFormErrors(err.response.data.data);
                message = self.$vuetify.lang.t("$vuetify.error_form");
              } else if (err.response.status === 403) {
                message = self.$vuetify.lang.t("$vuetify.error_denied");
              } else if (err.response.status === 500) {
                message = self.$vuetify.lang.t("$vuetify.error_server");
              }
            }
            self.$store.dispatch("showNotification", {
              status: true,
              text: message
            });
            reject(err);
          })
          .then(() => {
            self.loadingDetails.list = false;
            resolve();
          });
      });
    },
    // editDetails(item) {
    //   // this.dialogFormDetails.show = true;
    //   this.$refs.detailsForm.setForm(item);
    //   // this.refreshItemsDetails();
    // },
    saveDetails() {
      // console.log(this.getDataFormdetails());
      this.$store.dispatch("showNotification", {
        statut: false
      });
      let self = this;
      self.$v.$touch();
      if (!self.$v.$invalid) {
        this.loading = true;
        this.$store
          .dispatch("request", {
            url: self.getRouteDetails(),
            method: self.getMethod(),
            data: self.getDataFormdetails()
          })
          .then(() => {
            self.$store.dispatch("showNotification", {
              statut: true,
              text: self.$vuetify.lang.t("$vuetify.supplies.new.successDetails")
            });
            //console.log("Quantity", this.form.quantity);
            this.getItemsDetails();
            self.resetDataFormProduct();
          })
          .catch(error => {
            let message;
            if (error.response) {
              if (error.response.status === 400) {
                message = "$vuetify.error_occured";
                const fields = error.response.data;
                self.setFormErrors(fields);
                this.$forceUpdate();
              } else {
                message = "$vuetify.error_server";
              }
            }
            self.$store.dispatch("showNotification", {
              statut: true,
              text: self.$vuetify.lang.t(message),
              color: "#B71C1C"
            });
          })
          .then(() => {
            self.loading = false;
          });
      }
      /*else {
               console.log(self.$v);
             }*/
    }
  },
  computed: {
    get_product_code() {
      let product_code = "";
      product_code =
        this.form.details_stock === null
          ? ""
          : this.form.details_stock?.product.code;
      return product_code;
    },
    get_product_price() {
      let product_price = "";
      product_price =
        this.form.details_stock === null ? "" : this.form.details_stock?.cmup;
      return product_price;
    },
    get_movement_value() {
      let total = 0;
      this.items.forEach(d => {
        total += d.total_amount;
      });
      // let sum = this.items.reduce((a, b) => a + (b[key] || 0), 0)
      // console.log("hum",total);
      return total;
    },
    get_total_amount() {
      // console.log("hum",this.form.product.purchase_price);
      if (this.form.quantity && this.form.details_stock?.cmup) {
        return this.form.quantity * this.form.details_stock?.cmup;
      } else if (this.form.total_amount != null) {
        return this.form.total_amount;
      } else {
        return 0;
      }
    },
    // get_storage_depot() {
    //   if (this.form.operation_date) {
    //     const today = new Date();
    //     const date_year = today.getFullYear();
    //     return (
    //       date_year - moment(String(this.form.operation_date)).format("YYYY")
    //     );
    //   } else {
    //     return 0;
    //   }
    // },
    // Gestion des erreurs du formulaire

    nameErrors() {
      let errors = [];
      if (!this.$v.form.name.$dirty) return errors;
      !this.$v.form.name.required &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.stock_movement.cols.name.required")
        );
      // !this.$v.form.name.unique &&
      //   errors.push(
      //     this.$vuetify.lang.t("$vuetify.stock_movement.cols.name.unique")
      //   );
      return errors;
    },
    product_codeErrors() {
      let errors = [];
      if (!this.$v.form.product_code.$dirty) return errors;

      // !this.$v.form.product_code.required &&
      //   errors.push(
      //     this.$vuetify.lang.t("$vuetify.stock_movement.cols.product_code.required")
      //   );

      !this.$v.form.product_code.product_code &&
        errors.push(
          this.$vuetify.lang.t(
            "$vuetify.stock_movement.cols.product_code.invalid"
          )
        );

      !this.$v.form.product_code.unique &&
        errors.push(
          this.$vuetify.lang.t(
            "$vuetify.stock_movement.cols.product_code.unique"
          )
        );
      return errors;
    },
    type_movementErrors() {
      let errors = [];
      if (!this.$v.form.type_movement.$dirty) return errors;
      !this.$v.form.type_movement.required &&
        errors.push(
          this.$vuetify.lang.t(
            "$vuetify.stock_movement.cols.type_movement.required"
          )
        );
      return errors;
    },
    operation_dateErrors() {
      let errors = [];
      if (!this.$v.form.operation_date.$dirty) return errors;
      !this.$v.form.operation_date.required &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.stock_movement.cols.birthday.required")
        );
      return errors;
    },
    total_amountErrors() {
      let errors = [];
      if (!this.$v.form.total_amount.$dirty) return errors;
      !this.$v.form.total_amount.required &&
        errors.push(
          this.$vuetify.lang.t(
            "$vuetify.stock_movement.cols.total_amount.required"
          )
        );
      return errors;
    }
  },
  components: {
    DeleteItem
  }
};
</script>
